import React, { useState, useEffect } from 'react';
import { Button, Form, FormGroup, Label, Input, Alert } from 'reactstrap';
import axios from 'axios';

const initialForm = {
	fullName: '',
	number: '',
	email: '',
	subject: '',
	message: ''
};

const initialAlert = { message: '', type: 'primary', remove: false };

const ContactForm = props => {
	const [formData, setFormData] = useState(initialForm);
	const [isHuman, setIsHuman] = useState(false);
	const [alert, setAlert] = useState(initialAlert);

	useEffect(() => {
		if (alert.remove) {
			setTimeout(() => {
				setAlert(initialAlert);
			}, 9000);
		}
	}, [alert]);

	const inputChangeHandler = e => {
		e.preventDefault();
		setFormData({ ...formData, [e.target.name]: e.target.value });
	};

	const formSubmit = e => {
		e.preventDefault();
		if (!isHuman) {
			setAlert({ ...alert, message: 'Prove you a human!', type: 'danger' });
			return;
		}
		const { fullName, email, message } = formData;
		if (!fullName || !email || !message) {
			setAlert({ ...alert, message: `Make sure you are fill fullName, email, message fields`, type: 'danger' });
			return;
		}
		const _formData = new FormData();
		for (let field in formData) {
			if (formData[field]) {
				_formData.append(field, formData[field]);
			}
		}
		axios
			.post('https://techincent.com/wp-json/contact-form-7/v1/contact-forms/6184/feedback', _formData)
			.then(response => {
				console.log(response);
				if (response.data.status === 'mail_sent') {
					setAlert({
						...alert,
						message: `Successfully! I will reply you very short time`,
						type: 'success',
						remove: true
					});
					setFormData(initialForm);
					setIsHuman(false);
					return;
				}
				setAlert({
					...alert,
					message: response.message,
					type: 'error'
				});
			})
			.catch(error => {
				console.log(error);
			});
	};

	return (
		<Form onSubmit={formSubmit}>
			{alert.message && <Alert color={alert.type}>{alert.message}</Alert>}
			<div className="row">
				<div className="col-sm-6">
					<FormGroup>
						<Label for="name">Full Name*</Label>
						<Input
							onChange={inputChangeHandler}
							value={formData.fullName}
							type="text"
							name="fullName"
							id="name"
							required
						/>
					</FormGroup>
				</div>
				<div className="col-sm-6">
					<FormGroup>
						<Label for="subject">Subject</Label>
						<Input
							onChange={inputChangeHandler}
							value={formData.subject}
							type="text"
							name="subject"
							id="subject"
						/>
					</FormGroup>
				</div>
			</div>
			<div className="row mt-4">
				<div className="col-sm-6">
					<FormGroup>
						<Label for="email">Email*</Label>
						<Input
							onChange={inputChangeHandler}
							value={formData.email}
							type="email"
							name="email"
							id="email"
							required
						/>
					</FormGroup>
				</div>
				<div className="col-sm-6">
					<FormGroup>
						<Label for="number">Number</Label>
						<Input
							onChange={inputChangeHandler}
							value={formData.number}
							type="number"
							name="number"
							id="number"
						/>
					</FormGroup>
				</div>
			</div>
			<FormGroup className="mt-4">
				<Input
					style={{ height: '6rem' }}
					onChange={inputChangeHandler}
					value={formData.message}
					type="textarea"
					name="message"
					id="message"
					placeholder="Message*"
					required
				/>
			</FormGroup>
			<FormGroup className="mt-4" check>
				<Label check className="cursor-pointer">
					<Input
						type="checkbox"
						checked={isHuman}
						onChange={e => setIsHuman(!isHuman)}
						className="cursor-pointer"
					/>{' '}
					I'm not Robot
				</Label>
			</FormGroup>
			<Button type="submit" className="mt-4 btn-info px-5">
				Submit
			</Button>
		</Form>
	);
};

export default ContactForm;
