import React, { useState, useEffect } from 'react';
import { Section } from '@ti/elements/Section';
import Tilt from 'react-parallax-tilt';
import { SectionTitle, Item, ItemBody, Title, TitleHeading, Date, Thumbnail } from './Elements.portfolio';

const RenderPortfolio = ({ item }) => {
	return (
		<Tilt>
			<Item>
				<Thumbnail target="_blank" href={item.link}>
					<img top src={item.feature_images} alt={item.title.rendered} />
				</Thumbnail>
				<ItemBody>
					<Title target="_blank" href={item.link} className="text-dark">
						<TitleHeading>{item.title.rendered}</TitleHeading>
					</Title>
					{/* <Date className="mb-2 text-muted">{item.date}</Date> */}
				</ItemBody>
			</Item>
		</Tilt>
	);
};

const Portfolio = () => {
	const [portfolio, setPortfolio] = useState([]);

	useEffect(() => {
		fetch('https://techincent.com/wp-json/wp/v2/portfolio')
			.then(response => response.json())
			.then(data => {
				setPortfolio(data.slice(0, 6));
			})
			.catch(res => {
				console.log(res);
			});
	}, []);
	return (
		<Section padding="83px 0 90px" id="id_portfolio">
			<div className="container">
				<SectionTitle>
					Latest <span>Creative Portfolio</span>
				</SectionTitle>
				<div className="row mt-n3">
					{portfolio.length <= 0 ? (
						<div>No Item Found</div>
					) : (
						portfolio.map((item, index) => (
							<div className="col-sm-6 col-md-4 mt-5" key={index}>
								<RenderPortfolio item={item} />
							</div>
						))
					)}
				</div>
			</div>
		</Section>
	);
};

export default Portfolio;
